body {
    background-color: #ffffff !important;
    margin: 0;
    overflow: auto !important;
}

.tooltip-font {
    font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
}

.a4-layout {
    height: 100%;
    box-sizing: border-box;
    margin: 10px;
    max-width: 1024px;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
}

a {
    text-decoration: none;
}

.center {
    text-align: -webkit-center;
}

.bodyContainer {
    height: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #f3f3f3;
    font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
}

.bodyContainer>div {
    width: 100%;
    padding: 10px;
}

.sectionText {
    font-size: 14px;
    font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
    color: #8B87FA;
    align-items: center;
    text-align: left;
    border-bottom: 1px solid;
    background-color: #E5E5E5;
    padding: 5px;
}

.sectionContentText {
    text-align: left;
    color: #666583;
    padding: 10px;
}

.textBold {
    font-weight: bold;
}

.section {
    padding: 10px;
}

.sectionTopic {
    padding: 5px;
}


/* TOPO DO CV */
.head {
    box-sizing: border-box;
    height: 100px;
}

.headLeft {
    /* border-radius: 5px 0px 0px 0px; */
    width: calc(25px - 50%);
    float: left;
    background-color: #e5e5e5;
}

.headCenter {
    min-width: 50px;
    max-width: 50px;
    float: left;
    background-color: #e5e5e5;
    border-top: 100px solid #5025D1;
    border-left: 45px solid transparent;
    padding: 0px;
}

.headRight {
    padding: 3px;
    width: calc(25px - 50%);
    /* border-radius: 0px 5px 0px 0px; */
    float: left;
}

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}


.contact,
a {
    font-size: 15px;
    color: #f0f1ff;
    font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
}

.contactLines {
    align-items: center;
    text-align: right;
    padding: 2px;
}

.headName {
    font-size: 24px;
    height: 100px;
    color: #5025D1;
    font-weight: bold;
    font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
    padding: 5px;
}

.headContainer {
    display: flex;
    justify-content: space-between;
    background-color: #5025D1;
}

@media only screen and (max-width: 767px) {
    .sectionText {
        text-align: center;
        padding: 5px;
        border-bottom: 1px solid;
        background-color: #E5E5E5;
        padding: 3px;
    }

    .bodyContainer {
        display: flow;
    }

    .bodyContainer>div {
        padding: 5px;
        margin: 0px;
    }

    .sectionContentText {
        padding: 5px;
        text-align: center !important;
    }

    .contact>a {
        font-size: 13px;
    }

    .headName {
        font-size: 24px;
    }

    .headContainer {
        display: flow;
    }

    .headRight {
        background-color: #5025D1;
    }

    .contactLines {
        padding: 2px;
        text-align: center !important;
    }

    .headCenter {
        border: hidden;
    }

    .headContainer>div {
        width: 100%;
        margin: 0px;
        align-self: center;
    }

    table {
        width: 100%;
        text-align-last: right;
    }
}

.headContainer>div {
    width: 100%;
}


/* CONFIGURAÇÕES PARA IMPRESSÃO OU SALVAR PDF */
@media print {
    .headCenter {
        border-top: 100px solid transparent;
    }

    .copyIcon,
    .externalIcon {
        visibility: hidden;
    }
}



.wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.contactLines {
    display: block;
    text-decoration: none;
    text-align: right;
    position: relative;
    transition: all .35s;
    word-spacing: 5px;
}

.contactLines span {
    position: relative;
    z-index: 0;
}

.contactLines:after {
    position: absolute;
    top: 0;
    left: 0;
    margin-left: -100px;
    width: 0px;
    height: 100%;
    background: #E5E5E5;
}

.contactLines:hover {
    color: #666583;
    z-index: 4;
    cursor: pointer;
    background-color: #E5E5E5;
}

.pointer:hover{
    cursor: pointer;
    font-weight: bold;
}

a:hover:after {
    width: 100%;
    color: #666583;
    z-index: 4;
    transition: all .35s;
}

a:hover {
    color: #666583;
}

.contactLines:hover:after {
    width: 100%;
    z-index: -1;
}


.mini-container {
    text-align: center;
}


.subtitle{
    font-weight: bold;
}

.left {
    float: left;
    width: 50%;
    padding: 2px;
}

.icon, .externalIcon, .copyIcon{
    margin-bottom: -2px;
}

.right {
    float: right;
    width: 50%;
}

table {
    width: 100%;
}

td {

    width: 50%;
    text-align: right;
}

.simpleRight{
    text-align-last: right;
    padding-right: 10px;
}

.simpleLeft{
    text-align-last: left;
    padding-left: 10px;
    white-space: nowrap;
}


.credits{
    /* background-color: rgba(64, 224, 208, 0.445); */
    width: 30%;
    padding: 10px;
    div{
padding: 5px;
    }
}